<template>
	<div id="public-footer">
		<van-tabbar v-if="1">
			<van-tabbar-item :to="util.getUrl({path: '/pages/order/index'})">
				<div slot="icon" slot-scope="props" class="icon icon-order font-20" :style="{color:  util.isMenuActive('/pages/order/index') ? '#33aafc' : '#666'}"></div>
				<span :style="{color:  util.isMenuActive('/pages/order/index') ? '#33aafc' : '#666'}">外卖</span>
			</van-tabbar-item>
			<van-tabbar-item :to="util.getUrl({path: '/pages/order/tangshi/index'})">
				<div slot="icon" slot-scope="props" class="icon icon-shop font-20" :style="{color:  util.isMenuActive('/pages/order/tangshi/index') ? '#33aafc' : '#666'}"></div>
				<span :style="{color:  util.isMenuActive('/pages/order/tangshi/index') ? '#33aafc' : '#666'}">店内</span>
			</van-tabbar-item>
			<van-tabbar-item :to="util.getUrl({path: '/pages/shop/home'})">
				<div slot="icon" slot-scope="props" class="icon icon-apps font-20" :style="{color:  util.isMenuActive('/pages/shop/home') ? '#33aafc' : '#666'}"></div>
				<span :style="{color:  util.isMenuActive('/pages/shop/home') ? '#33aafc' : '#666'}">运营</span>
			</van-tabbar-item>
			<van-tabbar-item :to="util.getUrl({path: '/pages/shop/setting'})">
				<div slot="icon" slot-scope="props" class="icon icon-settings font-20" :style="{color:  util.isMenuActive('/pages/shop/setting') ? '#33aafc' : '#666'}"></div>
				<span :style="{color:  util.isMenuActive('/pages/shop/setting') ? '#33aafc' : '#666'}">设置</span>
			</van-tabbar-item>
		</van-tabbar>
		<ul class="tabbar-img van-hairline--top" v-else>
			<router-link tag="li" to="" class="tabbar-img-item">
				<img src="static/img/" alt="">
			</router-link>
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'PublicFooter',
		props: {
			showPreLoading: {
				type: Boolean || Number
			},
			menufooter: Object
		},
		data() {
			return {
				active: 0
			}
		}
	}
</script>

<style>
	#public-footer{
		position: absolute;
		z-index: 100;
	}
	#public-footer .van-icon{
		font-size: 20px;
	}
	#public-footer .van-tabbar-item--active {
	    color: #33aafc;
	}
	#public-footer .tabbar-img{
		position: fixed;
		right: 0;
		bottom: 0;
		left: 0;
		height: 50px;
		background-color: #fff;
		display: flex;
		align-item: center;
		justify-content: center;
		flex-flow: row nowrap;
	}
	#public-footer .tabbar-img .tabbar-img-item{
		width: 100%;
		height: 100%;
		text-align: center;
	}
	#public-footer .tabbar-img .tabbar-img-item img{
		width: 36px;
		height: 36px;
		margin-top: 7px;
	}
</style>



